import React, { useState, useEffect } from 'react';
import {
  Grid,
  Card,
  CardContent,
  CardActions,
  Typography,
  IconButton,
  Box,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  TextField,
  Alert,
  Chip,
  CircularProgress
} from '@mui/material';
import {
  Edit as EditIcon,
  Delete as DeleteIcon,
  Visibility as VisibilityIcon,
  VisibilityOff as VisibilityOffIcon,
  ContentCopy as CopyIcon
} from '@mui/icons-material';
import PasswordEntryForm from './PasswordEntryForm';
import { useAuth } from '../../contexts/AuthContext';
import passwordsService from '../../services/passwords';

const PasswordList = ({ entries, onUpdate, onDelete, isLoading = false }) => {
  const { user } = useAuth();
  const [editDialogOpen, setEditDialogOpen] = useState(false);
  const [selectedEntry, setSelectedEntry] = useState(null);
  const [showPassword, setShowPassword] = useState({});
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');

  // Removed excessive logging

  const handleEdit = (entry) => {
    setSelectedEntry(entry);
    setEditDialogOpen(true);
  };

  const handleDelete = async (entryId) => {
    if (onDelete) {
      try {
        await onDelete(entryId);
        setSuccess('Password entry deleted successfully');
        setTimeout(() => setSuccess(''), 3000);
      } catch (err) {
        setError(err?.message || 'Failed to delete password entry');
        setTimeout(() => setError(''), 3000);
      }
    } else if (onUpdate) {
      try {
        await passwordsService.deletePassword(entryId);
        onUpdate();
        setSuccess('Password entry deleted successfully');
      } catch (err) {
        console.error('Delete error:', err);
        setError(err?.message || 'Failed to delete password entry');
      }
    }
  };

  const handleUpdate = async (updatedData) => {
    setError('Edit feature coming soon');
    setEditDialogOpen(false);
  };

  const togglePasswordVisibility = async (entryId, encryptedPassword) => {
    console.log('Toggle called for:', entryId);
    const currentlyVisible = showPassword[entryId];
    
    if (currentlyVisible) {
      // Hide password
      setShowPassword(prevState => ({
        ...prevState,
        [entryId]: null
      }));
    } else {
      // Decrypt and show password
      try {
        const decrypted = await passwordsService.getDecryptedPassword(encryptedPassword);
        setShowPassword(prevState => ({
          ...prevState,
          [entryId]: decrypted
        }));
      } catch (err) {
        console.error('Decryption error:', err);
        setError('Failed to decrypt password');
      }
    }
  };

  const copyToClipboard = (text) => {
    navigator.clipboard.writeText(text);
    setSuccess('Copied to clipboard');
  };

  const canEditPassword = (entry) => {
    // Admin users can edit any password (check profile role)
    if (user?.profile?.role === 'admin' || user?.isAdmin) return true;
    
    // User can edit if they are the owner
    // Appwrite uses ownerId (string), MongoDB uses owner (object)
    const ownerId = entry.ownerId || entry.owner?._id || entry.owner?.$id || entry.owner;
    const userId = user?.$id || user?._id || user?.userId;
    if (ownerId === userId) return true;
    
    // User can edit if they are an admin of the group the password belongs to
    if (entry.group && entry.group.members) {
      const userMembership = entry.group.members.find(m => {
        const memberUserId = m.user?._id || m.user?.$id || m.user;
        return memberUserId === userId;
      });
      return userMembership?.role === 'admin';
    }
    
    return false;
  };

  const canViewPassword = (entry) => {
    // Admin users can view any password (check profile role)
    if (user?.profile?.role === 'admin' || user?.isAdmin) return true;
    
    // User can view if they are the owner
    // Appwrite uses ownerId (string), MongoDB uses owner (object)
    const ownerId = entry.ownerId || entry.owner?._id || entry.owner?.$id || entry.owner;
    const userId = user?.$id || user?._id || user?.userId;
    if (ownerId === userId) return true;
    
    // User can view if they are a member of the group the password belongs to
    if (entry.group && entry.group.members) {
      const userMembership = entry.group.members.find(m => {
        const memberUserId = m.user?._id || m.user?.$id || m.user;
        return memberUserId === userId;
      });
      return !!userMembership; // Return true if user is a member, regardless of role
    }
    
    return false;
  };

  // Group passwords by their groups
  // Appwrite entries use ownerId instead of owner, and may not have group structure
  const groupedPasswords = entries.reduce((acc, entry) => {
    // Check if entry has a group (Appwrite uses groupId, MongoDB uses group)
    const groupId = entry.groupId || entry.group?._id || entry.group?.$id;
    if (groupId) {
      if (!acc.groups[groupId]) {
        acc.groups[groupId] = {
          name: entry.group?.name || 'Group',
          entries: []
        };
      }
      acc.groups[groupId].entries.push(entry);
    } else {
      acc.personal.push(entry);
    }
    return acc;
  }, { personal: [], groups: {} });

  // Define PasswordCard component outside the return statement
  const PasswordCard = ({ entry }) => {
    // Appwrite uses $id, MongoDB uses _id
    const entryId = entry?.$id || entry?._id;
    if (!entry || !entryId) {
      return null; // Don't render if entry is invalid
    }
    const isVisible = showPassword[entryId] || false;

    return (
      <Card 
        sx={{ 
          height: '100%',
          display: 'flex',
          flexDirection: 'column',
          borderRadius: 1,
          boxShadow: 2,
          position: 'relative'
        }}
      >
        {entry.group && (
          <Chip
            label={entry.group.name}
            size="small"
            color="primary"
            sx={{ 
              position: 'absolute',
              top: 8,
              right: 8,
              fontSize: '0.75rem',
              height: '24px'
            }}
          />
        )}
        <CardContent sx={{ flexGrow: 1, pb: 1, pt: entry.group ? 4 : 2 }}>
          <Typography 
            variant="h6" 
            sx={{ 
              mb: 2,
              fontWeight: 'medium',
              color: 'primary.main',
              pr: entry.group ? 8 : 0
            }}
          >
            {entry.title}
          </Typography>
          
          {/* Owner info - Show name instead of ID */}
          {entry.ownerId && (
            <Box sx={{ display: 'flex', alignItems: 'center', mb: 1.5 }}>
              <Typography variant="body2" sx={{ mr: 1, minWidth: '80px', fontWeight: 'medium' }}>
                Owner:
              </Typography>
              <Typography variant="body2" sx={{ flexGrow: 1, color: 'text.secondary' }}>
                {entry.owner?.name || entry.ownerId || 'You'}
              </Typography>
            </Box>
          )}

          <Box sx={{ display: 'flex', alignItems: 'center', mb: 1.5 }}>
            <Typography variant="body2" sx={{ mr: 1, minWidth: '80px', fontWeight: 'medium' }}>
              Username:
            </Typography>
            <Typography variant="body2" sx={{ flexGrow: 1 }}>
              {entry.username}
            </Typography>
          </Box>

          <Box sx={{ display: 'flex', alignItems: 'center', mb: 1.5 }}>
            <Typography variant="body2" sx={{ mr: 1, minWidth: '80px', fontWeight: 'medium' }}>
              Password:
            </Typography>
            <Typography 
              variant="body2" 
              sx={{ 
                fontFamily: 'monospace',
                flexGrow: 1,
                bgcolor: 'action.hover',
                borderRadius: 1,
                p: 1
              }}
            >
              {isVisible && showPassword[entryId] ? showPassword[entryId] : ''}
            </Typography>
          </Box>

          {entry.url && (
            <Box sx={{ display: 'flex', alignItems: 'center', mb: 1.5 }}>
              <Typography variant="body2" sx={{ mr: 1, minWidth: '80px', fontWeight: 'medium' }}>
                URL:
              </Typography>
              <Typography 
                variant="body2" 
                sx={{ 
                  flexGrow: 1, 
                  color: 'text.secondary',
                  overflow: 'hidden',
                  textOverflow: 'ellipsis',
                  whiteSpace: 'nowrap',
                  '&:hover': {
                    color: 'primary.main',
                    textDecoration: 'underline',
                    cursor: 'pointer'
                  }
                }}
                onClick={() => entry.url && window.open(entry.url, '_blank')}
              >
                {entry.url}
              </Typography>
            </Box>
          )}

          {entry.notes && (
            <Box sx={{ display: 'flex', alignItems: 'flex-start', mt: 1 }}>
              <Typography variant="body2" sx={{ mr: 1, minWidth: '80px', fontWeight: 'medium' }}>
                Notes:
              </Typography>
              <Typography 
                variant="body2" 
                sx={{ 
                  color: 'text.secondary',
                  overflow: 'hidden',
                  display: '-webkit-box',
                  WebkitLineClamp: 3,
                  WebkitBoxOrient: 'vertical',
                  whiteSpace: 'pre-line',
                  wordBreak: 'break-word',
                  maxWidth: 'calc(100% - 90px)',
                  bgcolor: 'action.hover',
                  borderRadius: 1,
                  p: 1
                }}
              >
                {entry.notes}
              </Typography>
            </Box>
          )}
        </CardContent>

        <CardActions 
          sx={{ 
            px: 2, 
            py: 1, 
            borderTop: 1, 
            borderColor: 'divider',
            bgcolor: 'background.default'
          }}
        >
          <Box sx={{ display: 'flex', gap: 1, ml: 'auto' }}>
            <IconButton
              size="small"
              onClick={() => {
                console.log('Toggle clicked for:', entryId);
                togglePasswordVisibility(entryId, entry.encryptedPassword);
              }}
              color="primary"
              sx={{ 
                p: 0.5,
                '&:hover': {
                  bgcolor: 'primary.light',
                  color: 'primary.contrastText'
                }
              }}
              disabled={!canViewPassword(entry)}
            >
              {isVisible ? <VisibilityOffIcon fontSize="small" /> : <VisibilityIcon fontSize="small" />}
            </IconButton>
            <IconButton
              size="small"
              onClick={() => copyToClipboard(entry.password)}
              color="primary"
              sx={{ 
                p: 0.5,
                '&:hover': {
                  bgcolor: 'primary.light',
                  color: 'primary.contrastText'
                },
                '&.Mui-disabled': {
                  opacity: 0.5
                }
              }}
              disabled={!canViewPassword(entry)}
              title={!canViewPassword(entry) ? "You don't have permission to copy this password" : "Copy password"}
            >
              <CopyIcon fontSize="small" />
            </IconButton>
            <IconButton
              size="small"
              onClick={() => handleEdit(entry)}
              color="primary"
              sx={{ 
                p: 0.5,
                '&:hover': {
                  bgcolor: 'primary.light',
                  color: 'primary.contrastText'
                },
                '&.Mui-disabled': {
                  opacity: 0.5
                }
              }}
              title={canEditPassword(entry) ? "Edit password" : "Only owner and group admins can edit"}
              disabled={!canEditPassword(entry)}
            >
              <EditIcon fontSize="small" />
            </IconButton>
            <IconButton
              size="small"
              onClick={() => {
                if (window.confirm('Are you sure you want to delete this password?')) {
                  handleDelete(entryId);
                }
              }}
              color="error"
              sx={{ 
                p: 0.5,
                '&:hover': {
                  bgcolor: 'error.light',
                  color: 'error.contrastText'
                },
                '&.Mui-disabled': {
                  opacity: 0.5
                }
              }}
              title={canEditPassword(entry) ? "Delete password" : "Only owner and group admins can delete"}
              disabled={!canEditPassword(entry)}
            >
              <DeleteIcon fontSize="small" />
            </IconButton>
          </Box>
        </CardActions>
      </Card>
    );
  };

  if (isLoading) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', p: 4 }}>
        <CircularProgress />
      </Box>
    );
  }

  return (
    <Grid container spacing={3}>
      <Grid item xs={12}>
        <Typography variant="h5" sx={{ mb: 3, color: 'primary.main' }}>
          Personal Passwords
        </Typography>
        {groupedPasswords.personal.length === 0 ? (
          <Box sx={{ textAlign: 'center', py: 6 }}>
            <Typography variant="body1" color="text.secondary">
              No personal passwords yet. Create one to get started!
            </Typography>
          </Box>
        ) : (
          <Grid container spacing={3}>
            {groupedPasswords.personal.map((entry) => (
              <Grid item xs={12} sm={6} md={4} key={`personal-${entry._id || entry.$id}`}>
                <PasswordCard entry={entry} />
              </Grid>
            ))}
          </Grid>
        )}
      </Grid>

      {Object.entries(groupedPasswords.groups).map(([groupId, group]) => (
        <Grid item xs={12} key={`group-${groupId}`}>
          <Typography variant="h5" sx={{ mb: 3, mt: 4, color: 'primary.main' }}>
            {group.name} Passwords
          </Typography>
          <Grid container spacing={3}>
            {group.entries.map((entry) => (
              <Grid item xs={12} sm={6} md={4} key={`entry-${entry._id || entry.$id}`}>
                <PasswordCard entry={entry} />
              </Grid>
            ))}
          </Grid>
        </Grid>
      ))}

      <Dialog
        open={editDialogOpen}
        onClose={() => setEditDialogOpen(false)}
        maxWidth="sm"
        fullWidth
        PaperProps={{
          sx: {
            borderRadius: 1,
            boxShadow: 3,
            bgcolor: 'background.paper'
          }
        }}
      >
        <DialogTitle 
          sx={{ 
            pb: 1,
            borderBottom: 1,
            borderColor: 'divider',
            bgcolor: 'background.default'
          }}
        >
          Edit Password Entry
        </DialogTitle>
        <DialogContent sx={{ p: 2 }}>
          {selectedEntry && (
            <PasswordEntryForm
              entry={selectedEntry}
              onSubmit={handleUpdate}
              onCancel={() => setEditDialogOpen(false)}
            />
          )}
        </DialogContent>
      </Dialog>

      {(error || success) && (
        <Box 
          sx={{ 
            position: 'fixed',
            bottom: 16,
            right: 16,
            zIndex: 2000,
            display: 'flex',
            flexDirection: 'column',
            gap: 1
          }}
        >
          {error && (
            <Alert 
              severity="error" 
              variant="filled"
              onClose={() => setError('')}
              sx={{ 
                minWidth: '250px',
                boxShadow: 3
              }}
            >
              {error}
            </Alert>
          )}
          
          {success && (
            <Alert 
              severity="success"
              variant="filled"
              onClose={() => setSuccess('')}
              sx={{ 
                minWidth: '250px',
                boxShadow: 3
              }}
            >
              {success}
            </Alert>
          )}
        </Box>
      )}
    </Grid>
  );
};

export default PasswordList; 