import React, { useState } from 'react';
import {
  Grid,
  Card,
  CardContent,
  CardActions,
  Typography,
  IconButton,
  Box,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  TextField,
  Alert,
  List,
  ListItem,
  ListItemText,
  ListItemSecondaryAction,
  Chip
} from '@mui/material';
import {
  Edit as EditIcon,
  Delete as DeleteIcon,
  PersonAdd as PersonAddIcon,
  PersonRemove as PersonRemoveIcon
} from '@mui/icons-material';
import { useAuth } from '../../contexts/AuthContext';
import passwordGroupsService from '../../services/passwordGroups';

const PasswordGroupList = ({ groups, onUpdate }) => {
  const { user } = useAuth();
  const [editDialogOpen, setEditDialogOpen] = useState(false);
  const [addMemberDialogOpen, setAddMemberDialogOpen] = useState(false);
  const [selectedGroup, setSelectedGroup] = useState(null);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const [newMemberEmail, setNewMemberEmail] = useState('');
  const [newMemberNickname, setNewMemberNickname] = useState('');

  const handleEdit = (group) => {
    setSelectedGroup(group);
    setEditDialogOpen(true);
  };

  const handleDelete = async (groupId) => {
    try {
      await passwordGroupsService.deletePasswordGroup(groupId);
      onUpdate();
      setSuccess('Password group deleted successfully');
      setTimeout(() => setSuccess(''), 3000);
    } catch (err) {
      setError(err?.message || 'Failed to delete password group');
      setTimeout(() => setError(''), 5000);
    }
  };

  const handleAddMember = async () => {
    if (!newMemberEmail.trim()) {
      setError('Please enter an email address');
      setTimeout(() => setError(''), 3000);
      return;
    }
    
    try {
      await passwordGroupsService.addMemberToPasswordGroup(
        selectedGroup.$id || selectedGroup._id,
        newMemberEmail.trim(),
        newMemberNickname.trim()
      );
      onUpdate();
      setAddMemberDialogOpen(false);
      setNewMemberEmail('');
      setNewMemberNickname('');
      setSuccess('Member added successfully');
      setTimeout(() => setSuccess(''), 3000);
    } catch (err) {
      setError(err?.message || 'Failed to add member');
      setTimeout(() => setError(''), 5000);
    }
  };

  const handleRemoveMember = async (groupId, userId) => {
    try {
      await passwordGroupsService.removeMemberFromPasswordGroup(groupId, userId);
      onUpdate();
      setSuccess('Member removed successfully');
      setTimeout(() => setSuccess(''), 3000);
    } catch (err) {
      setError(err?.message || 'Failed to remove member');
      setTimeout(() => setError(''), 5000);
    }
  };

  return (
    <Grid container spacing={3}>
      {groups.length === 0 ? (
        <Grid item xs={12}>
          <Box sx={{ textAlign: 'center', py: 4 }}>
            <Typography variant="body1" color="text.secondary">
              No password groups yet. Create one to get started!
            </Typography>
          </Box>
        </Grid>
      ) : (
        groups.map((group) => (
          <Grid item xs={12} sm={6} md={4} key={group.$id || group._id}>
          <Card 
            sx={{ 
              height: '100%',
              display: 'flex',
              flexDirection: 'column',
              borderRadius: 1,
              boxShadow: 2
            }}
          >
            <CardContent sx={{ flexGrow: 1, pb: 1 }}>
              <Typography 
                variant="h6" 
                sx={{ 
                  mb: 1,
                  fontWeight: 'medium',
                  color: 'primary.main'
                }}
              >
                {group.name}
              </Typography>
              
              {group.description && (
                <Typography 
                  variant="body2" 
                  color="text.secondary" 
                  sx={{ 
                    mb: 2,
                    overflow: 'hidden',
                    display: '-webkit-box',
                    WebkitLineClamp: 2,
                    WebkitBoxOrient: 'vertical'
                  }}
                >
                  {group.description}
                </Typography>
              )}

              <Typography 
                variant="subtitle2" 
                sx={{ 
                  mb: 1,
                  fontWeight: 'medium',
                  color: 'text.primary'
                }}
              >
                Members:
              </Typography>
              
              <List sx={{ p: 0 }}>
                {group.membersWithInfo && Array.isArray(group.membersWithInfo) && group.membersWithInfo.length > 0 ? (
                  group.membersWithInfo.map((member, index) => {
                    const displayName = member.nickname || member.name || member.email || 'Unknown';
                    return (
                      <ListItem 
                        key={`${group.$id || group._id}-${member.userId}-${index}`}
                        sx={{
                          px: 0,
                          py: 0.75,
                          borderBottom: '1px solid',
                          borderColor: 'divider',
                          '&:last-child': {
                            borderBottom: 'none'
                          }
                        }}
                      >
                        <ListItemText
                          primary={displayName}
                          secondary={member.nickname ? `${member.name} (${member.email})` : member.email}
                          primaryTypographyProps={{
                            variant: 'body2',
                            fontWeight: 'medium',
                            sx: { mb: 0.25 }
                          }}
                          secondaryTypographyProps={{
                            variant: 'caption',
                            color: 'text.secondary'
                          }}
                          sx={{ mr: 1 }}
                        />
                        <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                          {group.membersWithInfo.length > 1 && (
                            <IconButton
                              size="small"
                              onClick={() => handleRemoveMember(group.$id || group._id, member.userId)}
                              color="error"
                              sx={{ p: 0.5 }}
                            >
                              <PersonRemoveIcon fontSize="small" />
                            </IconButton>
                          )}
                        </Box>
                      </ListItem>
                    );
                  })
                ) : (
                  <Typography variant="caption" color="text.secondary" sx={{ px: 2, py: 1 }}>
                    No members yet
                  </Typography>
                )}
              </List>
            </CardContent>

            <CardActions sx={{ px: 2, py: 1, borderTop: 1, borderColor: 'divider' }}>
              <Box sx={{ display: 'flex', gap: 1 }}>
                <IconButton
                  size="small"
                  onClick={() => {
                    setSelectedGroup(group);
                    setAddMemberDialogOpen(true);
                  }}
                  color="primary"
                  sx={{ p: 0.5 }}
                >
                  <PersonAddIcon fontSize="small" />
                </IconButton>
                <IconButton
                  size="small"
                  onClick={() => handleEdit(group)}
                  color="primary"
                  sx={{ p: 0.5 }}
                >
                  <EditIcon fontSize="small" />
                </IconButton>
                <IconButton
                  size="small"
                  onClick={() => handleDelete(group.$id || group._id)}
                  color="error"
                  sx={{ p: 0.5 }}
                >
                  <DeleteIcon fontSize="small" />
                </IconButton>
              </Box>
            </CardActions>
          </Card>
        </Grid>
        ))
      )}

      <Dialog
        open={addMemberDialogOpen}
        onClose={() => {
          setAddMemberDialogOpen(false);
          setNewMemberEmail('');
          setNewMemberNickname('');
        }}
        maxWidth="sm"
        fullWidth
        PaperProps={{
          sx: {
            borderRadius: 1,
            boxShadow: 3
          }
        }}
      >
        <DialogTitle sx={{ pb: 1 }}>Add Member to Group</DialogTitle>
        <DialogContent>
          <TextField
            autoFocus
            margin="dense"
            label="Email Address"
            type="email"
            fullWidth
            value={newMemberEmail}
            onChange={(e) => setNewMemberEmail(e.target.value)}
            size="small"
            required
            sx={{
              mt: 2,
              mb: 2,
              '& .MuiOutlinedInput-root': {
                borderRadius: 1
              }
            }}
          />
          <TextField
            margin="dense"
            label="Nickname (Optional)"
            fullWidth
            value={newMemberNickname}
            onChange={(e) => setNewMemberNickname(e.target.value)}
            size="small"
            placeholder="Enter a nickname for this member"
            helperText="Leave empty to use their name"
            sx={{
              '& .MuiOutlinedInput-root': {
                borderRadius: 1
              }
            }}
          />
        </DialogContent>
        <DialogActions sx={{ px: 3, pb: 2 }}>
          <Button 
            onClick={() => {
              setAddMemberDialogOpen(false);
              setNewMemberEmail('');
              setNewMemberNickname('');
            }}
            size="small"
            sx={{
              textTransform: 'none',
              fontWeight: 'medium'
            }}
          >
            Cancel
          </Button>
          <Button 
            onClick={handleAddMember} 
            variant="contained"
            size="small"
            disabled={!newMemberEmail.trim()}
            sx={{
              textTransform: 'none',
              fontWeight: 'medium'
            }}
          >
            Add Member
          </Button>
        </DialogActions>
      </Dialog>

      {(error || success) && (
        <Box 
          sx={{ 
            position: 'fixed',
            bottom: 16,
            right: 16,
            zIndex: 2000,
            display: 'flex',
            flexDirection: 'column',
            gap: 1
          }}
        >
          {error && (
            <Alert 
              severity="error" 
              variant="filled"
              onClose={() => setError('')}
              sx={{ 
                minWidth: '250px',
                boxShadow: 3
              }}
            >
              {error}
            </Alert>
          )}
          
          {success && (
            <Alert 
              severity="success"
              variant="filled"
              onClose={() => setSuccess('')}
              sx={{ 
                minWidth: '250px',
                boxShadow: 3
              }}
            >
              {success}
            </Alert>
          )}
        </Box>
      )}
    </Grid>
  );
};

export default PasswordGroupList; 