import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query';
import passwordsService from '../services/passwords';

export const usePasswords = (userId) => {
  return useQuery({
    queryKey: ['passwords', userId],
    queryFn: async () => {
      return await passwordsService.listPasswords(userId);
    },
    enabled: !!userId,
    staleTime: 10 * 60 * 1000, // 10 minutes
  });
};

export const usePasswordGroups = (userId) => {
  return useQuery({
    queryKey: ['passwordGroups', userId],
    queryFn: async () => {
      const passwordGroupsService = (await import('../services/passwordGroups')).default;
      return await passwordGroupsService.listPasswordGroups(userId);
    },
    enabled: !!userId,
    staleTime: 10 * 60 * 1000, // 10 minutes
  });
};

export const useCreatePassword = () => {
  const queryClient = useQueryClient();
  
  return useMutation({
    mutationFn: async ({ userId, passwordData }) => {
      return await passwordsService.createPassword(userId, passwordData);
    },
    onSuccess: (data, variables) => {
      queryClient.invalidateQueries(['passwords', variables.userId]);
    },
  });
};

export const useDeletePassword = () => {
  const queryClient = useQueryClient();
  
  return useMutation({
    mutationFn: async ({ passwordId, userId }) => {
      return await passwordsService.deletePassword(passwordId);
    },
    onSuccess: (data, variables) => {
      queryClient.invalidateQueries(['passwords', variables.userId]);
    },
  });
};

