import { databases } from '../appwrite';
import { Query, ID, Permission, Role } from 'appwrite';

const DATABASE_ID = process.env.REACT_APP_APPWRITE_DATABASE_ID || '69181c670024df0dd32b';
// Use the same groups collection for password groups (or create separate if needed)
const PASSWORD_GROUPS_COLLECTION_ID = process.env.REACT_APP_APPWRITE_COLLECTION_PASSWORD_GROUPS || '6918f637002e49bd5482';

export async function listPasswordGroups(userId = null) {
  try {
    const queries = [];
    // Filter by type='password' to distinguish from note groups
    queries.push(Query.equal('type', 'password'));
    if (userId) {
      queries.push(Query.equal('ownerId', userId));
    }
    const res = await databases.listDocuments(DATABASE_ID, PASSWORD_GROUPS_COLLECTION_ID, queries);
    const groups = res.documents || [];
    
    // Enrich members with profile information
    const PROFILES_COLLECTION_ID = process.env.REACT_APP_APPWRITE_COLLECTION_PROFILES || '6918f632002d42536ab2';
    const enrichedGroups = await Promise.all(groups.map(async (group) => {
      if (!group.members || !Array.isArray(group.members)) {
        return { ...group, membersWithInfo: [] };
      }
      
      // Parse member nicknames
      let memberNicknames = {};
      try {
        memberNicknames = group.memberNicknames ? JSON.parse(group.memberNicknames) : {};
      } catch (e) {
        memberNicknames = {};
      }
      
      // Fetch profiles for all member IDs
      const memberProfiles = await Promise.all(
        group.members.map(async (memberId) => {
          try {
            const profiles = await databases.listDocuments(
              DATABASE_ID,
              PROFILES_COLLECTION_ID,
              [Query.equal('userId', memberId)]
            );
            
            if (profiles.documents && profiles.documents.length > 0) {
              const profile = profiles.documents[0];
              return {
                userId: memberId,
                name: profile.name || profile.email || 'Unknown',
                email: profile.email || '',
                nickname: memberNicknames[memberId] || ''
              };
            }
            return {
              userId: memberId,
              name: 'Unknown User',
              email: '',
              nickname: memberNicknames[memberId] || ''
            };
          } catch (err) {
            console.warn(`Failed to fetch profile for ${memberId}:`, err);
            return {
              userId: memberId,
              name: 'Unknown User',
              email: '',
              nickname: memberNicknames[memberId] || ''
            };
          }
        })
      );
      
      return { ...group, membersWithInfo: memberProfiles };
    }));
    
    return enrichedGroups;
  } catch (err) {
    console.warn('Failed to fetch password groups:', err);
    return [];
  }
}

export async function createPasswordGroup(userId, groupData) {
  try {
    const { name, description } = groupData;
    if (!name) {
      throw new Error('Group name is required');
    }
    
    const permissions = [
      Permission.read(Role.user(userId)),
      Permission.update(Role.user(userId)),
      Permission.delete(Role.user(userId)),
      Permission.read(Role.users())
    ];
    
    const document = await databases.createDocument(
      DATABASE_ID,
      PASSWORD_GROUPS_COLLECTION_ID,
      ID.unique(),
      {
        ownerId: userId,
        name: name.trim(),
        description: description || '',
        members: [],
        memberNicknames: '{}', // JSON string for member nicknames
        type: 'password' // Distinguish from note groups
      },
      permissions
    );
    return document;
  } catch (err) {
    console.error('Failed to create password group:', err);
    throw err;
  }
}

export async function addMemberToPasswordGroup(groupId, userEmail, nickname = '') {
  try {
    const group = await databases.getDocument(DATABASE_ID, PASSWORD_GROUPS_COLLECTION_ID, groupId);
    
    const PROFILES_COLLECTION_ID = process.env.REACT_APP_APPWRITE_COLLECTION_PROFILES || '6918f632002d42536ab2';
    const profiles = await databases.listDocuments(
      DATABASE_ID,
      PROFILES_COLLECTION_ID,
      [Query.equal('email', userEmail.toLowerCase())]
    );
    
    if (!profiles.documents || profiles.documents.length === 0) {
      throw new Error('User not found with that email');
    }
    
    const userProfile = profiles.documents[0];
    const userId = userProfile.userId;
    
    // Get current members (array of user IDs) and nicknames (JSON object)
    const currentMembers = group.members || [];
    let memberNicknames = {};
    try {
      memberNicknames = group.memberNicknames ? JSON.parse(group.memberNicknames) : {};
    } catch (e) {
      memberNicknames = {};
    }
    
    // Check if user is already a member
    if (currentMembers.includes(userId)) {
      throw new Error('User is already a member of this group');
    }
    
    // Add user ID to members array and nickname to nicknames object
    const updatedMembers = [...currentMembers, userId];
    if (nickname.trim()) {
      memberNicknames[userId] = nickname.trim();
    }
    
    // Update with proper permissions
    const permissions = [
      Permission.read(Role.user(group.ownerId)),
      Permission.update(Role.user(group.ownerId)),
      Permission.delete(Role.user(group.ownerId)),
      Permission.read(Role.users())
    ];
    
    return await databases.updateDocument(
      DATABASE_ID,
      PASSWORD_GROUPS_COLLECTION_ID,
      groupId,
      {
        members: updatedMembers,
        memberNicknames: JSON.stringify(memberNicknames)
      },
      permissions
    );
  } catch (err) {
    console.error('Failed to add member:', err);
    throw err;
  }
}

export async function removeMemberFromPasswordGroup(groupId, userId) {
  try {
    const group = await databases.getDocument(DATABASE_ID, PASSWORD_GROUPS_COLLECTION_ID, groupId);
    const currentMembers = group.members || [];
    const updatedMembers = currentMembers.filter(m => m !== userId);
    
    // Remove nickname if exists
    let memberNicknames = {};
    try {
      memberNicknames = group.memberNicknames ? JSON.parse(group.memberNicknames) : {};
    } catch (e) {
      memberNicknames = {};
    }
    delete memberNicknames[userId];
    
    const permissions = [
      Permission.read(Role.user(group.ownerId)),
      Permission.update(Role.user(group.ownerId)),
      Permission.delete(Role.user(group.ownerId)),
      Permission.read(Role.users())
    ];
    
    return await databases.updateDocument(
      DATABASE_ID,
      PASSWORD_GROUPS_COLLECTION_ID,
      groupId,
      {
        members: updatedMembers,
        memberNicknames: JSON.stringify(memberNicknames)
      },
      permissions
    );
  } catch (err) {
    console.error('Failed to remove member:', err);
    throw err;
  }
}

export async function deletePasswordGroup(documentId) {
  try {
    return await databases.deleteDocument(DATABASE_ID, PASSWORD_GROUPS_COLLECTION_ID, documentId);
  } catch (err) {
    console.error('Failed to delete password group:', err);
    throw err;
  }
}

export default {
  listPasswordGroups,
  createPasswordGroup,
  addMemberToPasswordGroup,
  removeMemberFromPasswordGroup,
  deletePasswordGroup
};

