import CryptoJS from 'crypto-js';
import { databases } from '../appwrite';
import { Query, ID } from 'appwrite';

// Build timestamp: 2025-02-28 v7
const DATABASE_ID = process.env.REACT_APP_APPWRITE_DATABASE_ID || '69181c670024df0dd32b';
const PASSWORDS_COLLECTION_ID = process.env.REACT_APP_APPWRITE_COLLECTION_PASSWORDS || '6918f63900228f33e93c';
const ENCRYPTION_KEY = process.env.REACT_APP_ENCRYPTION_KEY || 'LOCAL_DEVELOPMENT_KEY';

function encrypt(text) {
  return CryptoJS.AES.encrypt(text, ENCRYPTION_KEY).toString();
}

function decrypt(cipher) {
  const bytes = CryptoJS.AES.decrypt(cipher, ENCRYPTION_KEY);
  return bytes.toString(CryptoJS.enc.Utf8);
}

// Create password without document-level permissions (using collection-level)
export async function createPassword(userId, entry) {
  console.log('[Password Manager] Creating password entry', { userId, entry, collectionId: PASSWORDS_COLLECTION_ID });
  
  if (!entry.title) {
    throw new Error('Title is required');
  }
  
  const payload = { ...entry, ownerId: userId };
  if (entry.password) {
    payload.encryptedPassword = encrypt(entry.password);
    delete payload.password;
  }
  
  // Ensure required fields are present
  if (!payload.encryptedPassword) {
    throw new Error('Password is required');
  }
  
  try {
    const result = await databases.createDocument(
      DATABASE_ID,
      PASSWORDS_COLLECTION_ID,
      ID.unique(),
      payload
    );
    console.log('[Password Manager] Password created successfully', result.$id);
    return result;
  } catch (error) {
    console.error('[Password Manager] Create error:', error);
    console.error('[Password Manager] Error details:', {
      message: error.message,
      code: error.code,
      type: error.type,
      response: error.response
    });
    throw error;
  }
}

export async function listPasswords(userId = null) {
  try {
    // If userId is provided, filter by owner. If null, list all (for admin)
    const queries = userId ? [Query.equal('ownerId', userId)] : [];
    const response = await databases.listDocuments(
      DATABASE_ID,
      PASSWORDS_COLLECTION_ID,
      queries
    );
    const entries = response.documents || [];
    
    // Enrich entries with owner profile information
    const PROFILES_COLLECTION_ID = process.env.REACT_APP_APPWRITE_COLLECTION_PROFILES || '6918f632002d42536ab2';
    const enrichedEntries = await Promise.all(entries.map(async (entry) => {
      if (!entry.ownerId) {
        return entry;
      }
      
      try {
        const profiles = await databases.listDocuments(
          DATABASE_ID,
          PROFILES_COLLECTION_ID,
          [Query.equal('userId', entry.ownerId)]
        );
        
        if (profiles.documents && profiles.documents.length > 0) {
          const profile = profiles.documents[0];
          return {
            ...entry,
            owner: {
              name: profile.name || profile.email || 'Unknown',
              email: profile.email || ''
            }
          };
        }
        return {
          ...entry,
          owner: {
            name: 'Unknown User',
            email: ''
          }
        };
      } catch (err) {
        console.warn(`Failed to fetch profile for owner ${entry.ownerId}:`, err);
        return {
          ...entry,
          owner: {
            name: 'Unknown User',
            email: ''
          }
        };
      }
    }));
    
    return enrichedEntries;
  } catch (err) {
    console.warn('Failed to fetch passwords:', err);
    return [];
  }
}

export async function deletePassword(documentId) {
  return databases.deleteDocument(DATABASE_ID, PASSWORDS_COLLECTION_ID, documentId);
}

export async function getDecryptedPassword(encrypted) {
  try {
    return decrypt(encrypted);
  } catch (e) {
    console.error('Decrypt error', e);
    throw e;
  }
}

export default { createPassword, listPasswords, getDecryptedPassword, deletePassword };
